.status {
  background:  $off-white;
  padding-bottom: 100px;
  img.baraban {
    position: absolute;
    top: -350px;
    z-index: 3; }
  &__block {
    margin-top: 370px;
    span {
      font-family: 'PFDinTextCompPro';
      font-size: 300px;
