.pop-up {
  display: none;
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background: $white;
  z-index: 7;
  overflow: scroll;
  &__content {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 100%;
    ul {
      margin-bottom: 50px;
      margin-top: 100px; }
    li {
      text-align: center;
      a {
        font-size: 30px;
        font-weight: bold;
        color: $crimson;
        margin-bottom: 12px;
        display: inline-block;
        &:hover {
          text-decoration: underline; } } } } }
